.footer{
    padding: 60px 0 0;
    background-image: url('../../../img/Nike-Air-Max.jpg');
    background-size: cover;
    background-attachment: fixed;
    background-position: bottom;
    position: relative;
    z-index: 1;
}
.row{
    display: flex;
    align-items: top;
    justify-content: space-between;
}
.footer::before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color:rgba(0,0,0,0.7);
    z-index: -1;
}
.footer-item{
    width: calc(100% / 3);
    padding: 0 15px;
    text-align: center;
}
.footer-item h3{
    text-transform: capitalize;
    font-size: 22px;
    margin: 0 0 15px;
    color: honeydew;
}
.footer-item p{
    line-height: 30px;
    color: honeydew;
}
.footer-item .social-links{
    margin-top: 15px;
}
.footer-item .social-links a{
    display: inline-flex;
    height: 40px;
    width: 40px;
    color: var(--white-light);
    font-size: 22px;
    align-items: center;
    justify-content: center;
    transition: color .3s ease;
}
.footer-item .social-links a:hover{
    color: var(--main-color);
}
.footer .copyright{
    margin: 80px 0 0;
    width: 100%; 
    text-align: center;
    padding: 25px 15px;
    font-size: 14px;
    border-top: 1px solid var(--white-light);
    color: honeydew;
}