.cart{
   padding: 100px 0;
   
}
.item-list{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-wrap: wrap;
}
.item-list > li{
    position: relative;
    width: 350px;
    background: linear-gradient(280deg, white, gold);
    border-radius: 15px;
    box-shadow: var(--shadow);
    height: 450px;
    margin-bottom: 30px;
    margin:0 25px 30px 25px;
    padding: 15px;
}
.item-list > li:nth-child(2n){
    background: linear-gradient(180deg, gold, white);
}
.item-info-box > h2{
    color: goldenrod;
    margin:10px 0 ; 
    font-size: 22px;
}
.item-big-btn{
    display: flex;
    align-items: center;
    justify-content: end;
    position: absolute;
    bottom: 15px;
}
.item-big-btn > h4{
    font-size: 20px;
    font-weight: bold;
    color: #0f0;
    margin-left: 10px;
}
.descition{
    margin: 20px 0;
    font-size: 15px;
    color: #999;
    font-weight: 600;
}
.item-img-box{
    width: 50%;
}
.item-img-box > img{
    width: 320px;
    height: 250px;
    object-fit: cover;
    border-radius: 10px;
}
.minus{
    width: 50px;
    height: 100%;
    border: none;
    outline: none;
    background:none;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
}
.minus > i{
    color: #777;
}
.item-btn-box > span{
    font-size: 20px;
    font-weight: bold;
}
.box{
    display: flex;
    align-items: top;
    justify-content: space-between;
}
.goHomess, .total{
    border: none;
    font-size: 23px;
    font-weight: 700;
    display: flex;
    color: goldenrod;
    align-items: center;
    margin-left: 10px;
    margin-bottom: 30px;
    font-family: 'Oleo Script', cursive;
}
.goHome > i{
    font-size: 30px;
    font-weight: 500;
    color: goldenrod;
}
.nocart{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
    margin-top: 190px;
    color: #999;
}
.icons-box{
    position: absolute;
    top: -30px;
}
.icons-box:nth-child(1){
    top: -110px;
    left: 520px;
}
.icons-box:nth-child(2){
    top: -100px;
    left: 650px;
}
.icons-box:nth-child(3){
    left: 575px;
    bottom: 50px;
}
.shopping-box{
    background:  rgb(238, 238, 238);
    width: 100%;
    height: 60px;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.shopping{
    padding: 10px 75px;
    border-radius: 40px;
    outline: none;
    border: none;
    background: gold;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    transition: .5s ease-in;
    font-family: 'Oleo Script', cursive;
 }
 .shopping:hover{
  background: goldenrod;
 }