header{
    padding: 10px 0;
    position: fixed;
    width: 100%;
    z-index: 10;
    background: gold;
    box-shadow: var(--shadow);
}
.brand-name{
    font-size: 30px;
    color: #fff;
}
.nav-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
form{
    width: 500px;
}
form{
    position: relative;
}
form > input{
    outline: none;
    width: 100%;
    padding: 10px 25px;
    font-size: 16px;
    border-radius: 10px;
    border: none;
    background: #fff;
    font-weight: bold;

}
form> i{
    position: absolute;
    right: 10px;
    font-size: 25px;
    font-weight: bold;
    top: 8px;
}
.korzinka-box{
    display: flex;
    align-items: center;
}
.kor-box{
    position: relative;
}
.kor-link > i{
    font-size: 35px;
    font-weight: bold;
    margin:0 20px;
    color: rgb(238, 238, 238);
    transition: .5s;
}
.kor-link > i:hover{
    color: #999;
}
.kor-link > .spans{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -5px;
    right: 10px;
    background: red;
    color: #fff;
    padding: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
}
.user-box{
    position: relative;
}
.user-span{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -5px;
    right: 10px;
    background: red;
    color: #fff;
    padding: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
}
.user-box{
    position: relative;
}
.user-box > .user-link > i{
    font-size: 50px;
    font-weight: bold;
    margin:0 10px;
    color: rgb(238, 238, 238);
    display: inline-block;
    transition: .5s;
}
.user-box > .user-link > i:hover{
    color: #999;
}

.user-info-box{
    box-shadow: var(--shadow);
    position: absolute;
    right: -75px;
    border-radius: 20px;
    padding: 15px;
    width: 400px;
    visibility: hidden;
    transition: .5s ease;
    background: linear-gradient(180deg, white, gold);
}
.user-profil{
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    color: goldenrod;
    border-bottom: 2px solid;
    margin-bottom: 15px;
    padding: 7px;
}
.user-name{
    font-size: 20px;
    color: gray;
    font-weight: 600;
}
.user-email{
    margin: 15px 0;
    font-size: 18px;
    font-weight: bold;
    color: gray;
}
.user-box:hover .user-info-box{
    visibility: visible;
   }
   .log-out{
    padding: 5px 15px;
    border-radius: 50px;
    border: 2px solid transparent;
    font-size: 18px;
    font-weight: bold;
    background: #000;
    color: gold;
    width: 100%;
    font-family: 'Oleo Script', cursive;
    cursor: pointer;
   }