.card{
    padding: 100px 0;
    height: 100%;
}
.card-list{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    height: 390px;
}
.card-list >li{
    border-radius: 50px;
    width: 85%;
    height: 100%;
    background: linear-gradient(120deg, gold, white);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}
 br{
     display: none;
 }
.items-img-box{
    height: 100%;
}
.items-img-box > img{
   width: 350px;
   height: 350px;
   border-radius: 40px;
}
.items-info-box{
    width: 60%;
}
.items-info-box > h2 {
    font-size: 23px;
    color: #444;
    opacity: 0.7;
}
.items-info-box > p {
    font-size: 18px;
    color: #444;
    opacity: 0.7;
    margin: 20px 0;
}
.btn-boxs{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.btn-add{
    padding: 10px 75px;
    height: 100%;
    border-radius: 40px;
    outline: none;
    border: none;
    background: #999;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    transition: .5s ease-in;
    font-family: 'Oleo Script', cursive;
 }
 .btn-add:hover{
  background: goldenrod;
 }
 .price{
     margin-right: 50px;
     font-size: 20px;
     font-weight: bold;
 }
 .goHomes{
    border: none;
    font-size: 23px;
    font-weight: 700;
    display: flex;
    color: goldenrod;
    align-items: center;
    margin-left: 10px;
    margin-bottom: 30px;
    font-family: 'Oleo Script', cursive;
}
.goHomes > i{
    font-size: 30px;
    font-weight: 500;
    color: goldenrod;
}