.goHome{
    border: none;
    font-size: 23px;
    font-weight: 700;
    display: flex;
    color: goldenrod;
    align-items: center;
    margin-left: 50px;
    margin-top: 30px;
    font-family: 'Oleo Script', cursive;
}
.goHome > i{
    font-size: 30px;
    font-weight: 500;
    color: goldenrod;
}
.form-box{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(192, 190, 190);
    border-radius: 15px;
    width: 500px;
    height: 430px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 25px;
    flex-direction: column;
    box-shadow: var(--shadow);
}
.login-title{
    top: -90px;
    left: 48%;
    transform: translateX(-45%);
    position: absolute;
    color: goldenrod;
    text-align: center;
    font-size: 120px;
}
.form-box > form{
    width: 100%;
    height: 100%;
}
.box{
    display: flex;
    align-items: center;
}
.box > i{
    font-size: 25px;
    font-weight: bold;
    color: #555;
    margin-top: 18px;
    margin-right: 8px;
}
.box > input{
    border: none;
    outline: none;
    margin-top: 20px;
    background: none;
    padding: 10px;
    border-bottom: 2px solid #555;
    width: 100%;
    color: #fff;
    border-radius: 0;
    font-size: 16px;
    font-weight: bold;
    transition: .5s;
}
.box > input:focus{
    border-color: var(--main-color);
}
.box > input:focus i{
    color: var(--main-color);
}
.btn-boxss{
    margin-top:30px;
    display: flex;
    align-items: center;
}
.login-btn{
    padding: 7px 45px;
    border-radius: 50px;
    border: 2px solid transparent;
    font-size: 23px;
    font-weight: bold;
    background: #000;
    color: gold;
    box-shadow: var(--shadow);
    transition: .5s;
    font-family: 'Oleo Script', cursive;

}
.login-btn:hover{
    background: #444;
}
.forget__link{
    display: block;
    color: #000;
    font-size: 18px;
    margin-left: 30px;
    transition: .5s;
}
.forget__link:hover{
    color: goldenrod;
}
.social__links{
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .social__links p{
    color: var(--light-color);
    font-weight: bold;
    font-size: 18px;
  }
  .social__links i{
    height: 30px;
    font-size: 20px;
    width: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    color: #333;
    cursor: pointer;
  }
  .social__links i:nth-child(2){
    background: #db4a39;
  }
  .social__links i:nth-child(3){
    background: #4267B2;
  }
  .social__links i:nth-child(4){
    background: #1DA1F2;
  }
  .social__links i:nth-child(5){
    background: #fbad50;
  }