.home{
    padding-top: 120px;
    padding-bottom: 50px;
  }
  .product-list{
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    .product-list >li{
      position: relative;
      width: 275px;
      height: 400px;
      border-radius: 50px;
      box-shadow: var(--shadow);
      margin-bottom: 30px;
      padding: 15px;
      background: #fff;
      cursor: pointer;
      transition: .5s ease;
      overflow: hidden;
      background: linear-gradient(180deg, gold, white);
    }
    .product-list >li:nth-child(2n){
      background: linear-gradient(125deg, white, gold);
    }
    .product-list>li:hover{
      box-shadow: none;
      transform: translateY(-10px) scale(1.05);
    }
    .product-list >li::before{
      content: 'Nike';
      position: absolute;
      right: -160px;
      bottom: 25px;
      font-size: 80px;
      font-weight: bold;
      color: rgb(224, 224, 224);
      font-style: italic;
      z-index: 0;
      font-family: 'Oleo Script', cursive;
      transition: .7s ease-in;
    }
   
    .product-list>li:hover::before{
      right: 20px;
    }
    .product-list > li:hover .shoes-img{
        transform: scale(1.05);
        border-radius: 15px;
    }
    .img-box{
      overflow: hidden;
      width: 100%;
      height: 190px;
      border-radius: 40px;
    }
    .img-box > img{
      width: 100%;
      height: 100%;
     border-radius: 40pz;
      transition: .5s ease;
    }
    .info-box{
      position: relative;
      padding: 10px;
    }
    .product-title{
      text-transform: capitalize;
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 5px;
      color: goldenrod;
    }
    .product-price{
     margin-bottom: 20px;
     color: #0f0;
    }
    .btn-box{
      position: absolute;
      bottom:18px;
      border-radius: 20px;
    }
   .add{
      padding: 10px 75px;
      width: 100%;
      height: 100%;
      border-radius: 40px;
      outline: none;
      border: none;
      background: gold;
      color: #fff;
      font-size: 20px;
      font-weight: 700;
      cursor: pointer;
      transition: .5s ease-in;
      font-family: 'Oleo Script', cursive;
   }
   .add:hover{
    background: goldenrod;
   }
   .nocarts{
    text-align: center;
    display: flex;
    align-items: center;
    font-size: 35px;
    margin: 50px 0;
    margin-left: 460px;
    color: #999;
   }
   .nobox{
     font-size: 50px;
     margin-left: 10px;
   }


  
   